.popup-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100000000;
}

.modal h1 {
  color: var(--secondary-color);
}

.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  width: calc(100% - 100px);
  max-width: 800px;
  max-height: calc(100vh - 40px);
  overflow-y: auto;
  margin: 20px;
  z-index: 1000000;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
}

.images-container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 20px;
  text-align: center;
}

.saved-image-container {
  flex: 1 1 calc(33.333% - 16px);

  box-sizing: border-box;
  padding: 8px;
}

.saved-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  display: block;
}

.image-wrapper {
  position: relative;
}

.delete-scratch-button {
  position: absolute;
  top: 5px;
  right: 5px;
  background: rgba(0, 0, 0, 0.1);
  color: black;
  border: none;
  border-radius: 25px;
  padding: 5px 5px;
  cursor: pointer;
}

.delete-scratch-button:hover {
  color: black;
}

.confirm-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border: 1px solid #ccc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

.confirm-popup button {
  margin: 5px;
}


@media (max-width:1024px) and (min-width:820px) {
  .popup-form-overlay {
    z-index: 900000;

  }
}